<template>
    <div class="info">
        <div class="profile"><img :src="userdata.profile" alt=""></div>
            <div class="informa">
                <p class="name">{{userdata.username}}</p>
                <table>
                <tbody>
                    <tr>
                        <td>性别：</td>
                        <td><span v-show="!editable" v-text="userdata.gender==1?'男':(userdata.gender==2?'女':'unknown')"></span>
                            <span v-show="editable"><input type="radio" value="1" name='gender' id="gender1"><label for="gender1">男</label> &nbsp; <input type="radio" value="2" name='gender' id="gender2"><label for="gender2">女</label></span>
                        </td>
                    </tr>
                    <tr>
                        <td><label for="location">所在地：</label></td>
                        <td><input type="text" name="location" id="location" :class="[editable?editing:unedit]" :disabled="!editable" v-model="userdata.location"></td>
                    </tr>
                    <tr>
                        <td><label for="self_intro">自我介绍：</label></td>
                        <td><input type="text" id="self_intro" name="self_intro" :class="[editable?editing:unedit]" :disabled="!editable" :value="userdata.intro"></td>
                    </tr>
                    <tr>
                        <td>注册时间：</td>
                        <td><span>{{userdata.rig_time}}</span></td>
                    </tr>
                    <tr>
                        <td>最近登录：</td>
                        <td><span>{{userdata.last_log}}</span></td>
                    </tr>
                </tbody>
                <input type="button" value="保存" v-show="editable" class="submit" @click="submit_info"><input type="button" value="取消"  v-show="editable" class="cancel" @click="editable=false">
                </table>
                <p class='edit_info' @click='eneditable'>编辑个人信息</p>
            </div>
            {{userinfo}}
        </div>
</template>


<script>
export default{
    props:{

    },
    data(){
        return{
            editable:false,
            editing:"editing",
            unedit:"unedit",
            userdata:{
                profile:"",
                gender:0,
                location:"初始值",
                rig_time:"1970-1-1",
                last_log:"1970-1-2",
                intro:"初始值"
                }
        }
    },
    methods:{
        eneditable(){
            this.editable=true;
        },
        submit_info(){
            this.editable=false;
            this.$axios.post("/data",this.userinfo).then((res)=>{alert("修改成功"+res)}).catch((err)=>{alert("修改失败"+err)})
        },

    },
    created(){
        this.$axios.get("/user/data"+location.search)
        .then((response)=>{this.userdata=response.data;console.log(this.userdata)})
        .catch((error)=>{alert(error)});
    }
}
</script>


<style scope>
.info{
	margin:30pt 100pt;
}
.profile{
	width:150pt;
	height:150pt;
	float:left;
	border:1px solid black;
}
.profile img{
	width:150pt;
	height:150pt;
}
.informa{
	position:relative;
	margin-left:200pt;
	width:400pt;
	height:150pt;
}
.name{
	font-size:16pt;
	background:-webkit-linear-gradient(0deg,rgb(230,235,255),rgb(200,200,255));
	background:linear-gradient(0deg,rgb(230,235,255),rgb(200,200,255));
}
.edit_info{
	position:absolute;
	bottom:0pt;
	right:0pt;
    border:1px solid #CDF;
    color:#09C;
    border-radius:3px;
    width:7em;
    height:1.5em;
    text-align: center;
    line-height: 1.5em;
    background: #EEF;
}
.edit_info:hover{
    background: #CDF;
}
.editing{
    width:200pt;
    font-size:1rem;
}
.unedit{
    background:#FFF;
    color:#000;
    border:none;
    outline:none;
    font-size:1rem;
}
.submit{
    margin-left:20pt;
    background-color:#08F;
    font-size:1rem;
    color:#FFF;
    padding:0.3em;
    border:solid 1px #08F;
    border-radius:2px ;
}
.cancel{
    margin-left:20pt;
    font-size:1rem;
    padding:0.3em;
    background:#FFF;
    color:#666;
    border-radius:2px ;
    border:1px solid #666;
}
</style>